import React from 'react';

const TestingReport = ({ data ,user,saledetails}) => {
  const groupedData = {};
  data.forEach((item) => {
    if (!groupedData[item.factory_name]) {
      groupedData[item.factory_name] = [];
    }
    groupedData[item.factory_name].push(item);
  });

  const handlePrintButtonClick = () => {
    const invoicesHtmlArray = [];
    const pageSize = 2; // Number of invoices per page
    const totalInvoices = Object.keys(groupedData).length;

    for (let i = 0; i < totalInvoices; i += pageSize) {
      const groupFactoryNames = Object.keys(groupedData).slice(i, i + pageSize);

      let invoicesHtml = '';
      groupFactoryNames.forEach((factoryName) => {
        const invoiceContent = createHtmlData(factoryName);
        invoicesHtml += invoiceContent;
      });

      invoicesHtmlArray.push(invoicesHtml);
    }

    const htmlContent = invoicesHtmlArray.map((invoicesHtml, index) => `
      <html>
      <head>
        <title>Tasting Report</title>
        <link rel="icon"  type="image/png" href="/ttab.png" />
        <style>
          .table-wrapper {
            margin: 0 auto;
            width: 900px;
            padding: 20px;
            background-color: #ffffff;
          }
          .tocontainer {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
          }
          body {
            font-family: "Courier New", Courier, monospace;
            color: #000;
            background: #757677;
            font-size: 12px;
          }
          table {
            width: 800px;
            margin: 0 auto;
            border-collapse: collapse;
          }
          th,
          td {
            padding: 5px;
            border: 1px solid black;
            text-align: center;
          }
          th.align-right,
          td.align-right {
            text-align: right;
          }
          .total {
            font-weight: bold;
            color: purple;
          }
          .green-text {
            font-weight: 400;
            color: purple;
          }
          .red-text {
            color: red;
          }
          .season {
            display: flex;
            align-items: space-between;
            justify-content: center;
          }
          @media print {
            body {
              margin: 0;
              font-size: 10px;
            }
            .table-wrapper {
              page-break-inside: avoid;
            }
          }
        </style>
      </head>
      <body>
        <div class="table-wrapper">
          ${invoicesHtml}
        </div>
        ${index < invoicesHtmlArray.length - 1 ? '<div style="page-break-before: always;"></div>' : ''}
      </body>
    </html>
    `);
    const newWindow = window.open('', '_blank');
    newWindow.document.write(htmlContent.join(''));
    newWindow.document.close();
  };

  const createHtmlData = (factoryName) => {
    const factoryData = groupedData[factoryName];
    let totalPkgs = 0;
    let grossTotal = 0;
    factoryData.forEach((item) => {
      totalPkgs += item.pkgs;
      grossTotal += Number(item.gross_total);
    });
    return `
      <div key=${factoryName} style="margin-bottom: 55px;">
        <div class="header">
        <h2 style="text-align: center;">${user}</h2>
        <h3 style="text-align: center;">Season : ${saledetails[0].season}</h3>
          <div class="season">
            <span style="text-align: center;">Sale No: ${saledetails[0].sale_no}</span>
            <span style="text-align: right;">Generated on: 6</span>
          </div>
          <hr />
          <div class="tocontainer">
            <div><span>To</span></div>
            <div><span>Manager</span></div>
            <div style="text-align: right;">${factoryName}</div>
          </div>
          <h2 style="text-align:center">${factoryName}</h2>
          <div class="tocontainer">
            <div><span> <span style="text-align: left;">Dear Sir,</span></span></div>
            <div style="text-align: right;">
              <span style="text-align: left;">We report on the following teas due for the above sale.</span>
            </div>
          </div>
        </div>
        <hr />
        <table border="1">
          <thead>
            <tr>
              <th>Lot No</th>
              <th>Invoice</th>
              <th>Grade</th>
              <th>Pkgs</th>
              <th>Price</th>
              <th>Maximum Bid</th>
              <th>LRating</th>
              <th>LIRating</th>
            </tr>
          </thead>
          <tbody>
            ${factoryData
              .map(
                (item) => `<tr key=${item.id}>
                  <td>${item.lot}</td>
                  <td>${item.invoice}</td>
                  <td>${item.grade}</td>
                  <td>${item.pkgs}</td>
                  <td>${item.price}</td>
                  <td>${item.maximum_bid !== 0 ? item.maximum_bid : ''}</td>
                  <td>${item.lrating_name}</td>
                  <td>${item.lirating_name}</td>
                </tr>`
              )
              .join('')}
          </tbody>
          <tfoot>
          <tr>
            <td colspan="2">Total :</td>
            <td>${totalPkgs} Pkgs</td>
            <td>${grossTotal} Kg</td>
          
            </tr>
            </tfoot>
        </table>
      </div>
    `;
  };

  return (
       <button className="btn btn-primary w-100 text-center mb-2 d-flex justify-content-between align-items-center" onClick={handlePrintButtonClick}>
  <span className="text-center mx-auto">Tasting Report</span>
  <span className="icon my-auto">&#x1F512;</span>
  </button>
  );
};

export default TestingReport;
