import React from "react";
    // const storedValue = localStorage.getItem("isVisible");
    // const str = JSON.parse(storedValue);
    // console.log(typeof(str));
let tableCount = 1;

function generateCategoryTable(data,user,saledetails) {

  const categories = [...new Set(data.map(item => item.factory_name))];
  const invoices = [...new Set(data.map(item => item.factory_name))];

  let tableHTML = '';
tableHTML=` <html>
<title>Catalogue</title>
<link rel="icon"  type="image/png" href="/ttab.png" />
<head>
  <meta charset="UTF-8">
  <style>
  table {
    border-collapse: collapse;
   
  }
  
  td {
    width: 150px;
    padding: 5px;
    border: none;
    text-align: center;
    font-size: 16px;
    color: black;
  }
  
    .table-wrapper {
        margin: 0 auto;
        width: 900px;
        padding: 20px;
        background-color: #ffffff;
        
    
    }
    td.top-border {
      border-top: 2px solid black;
    }
    
    td.bottom-lines {
      position: relative;
    }
    
    td.bottom-lines::after {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      border-bottom: 1px solid black;
    }
    
    td.bottom-lines::before {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      border-bottom: 1px solid black;
    }
    
    
    
    #t-header .h1{
      padding:0px;
    }
    .brokers-n{
      padding:0px;
      text-align:center;
    }
    .season{
      text-align:center;

    }
    .tbl-data{
      display: flex;
  justify-content: center;
  align-items: center;
    }
    .factory-name{
      text-align:center;
    }
    
    body{
      font-family: 'Courier New', Courier, monospace;
      color: #000;
background: #ededed;
font-size: 12px;
    }

    @media print {
        body * {
          visibility: hidden;
        }
        .table-wrapper, .table-wrapper * {
          visibility: visible;
        }

        .header-print {
            position: fixed;
            top: 0;
            left: 0;
            padding: 10px;
            font-size: 24px;
            font-weight: bold;
            text-align: center;
            width: 100%;
          }
      
      }
      .header {
         
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    /* Icon in the first row */
    .icon {
        width: 60px;
        height: 60px;
           }

    /* Span Container */
    .span-container {
        display: flex;
        flex-direction: column;
        align-items: center; /* Center the container horizontally */
    }

    /* Individual Spans */
    .span1, .span2, .span3 {
        padding: 2px 10px;
        text-align: center;
    }

    .span1 {
        font-size: 26px;
        font-weight:bold;
    }

    .span2, .span3 {
        font-size: 14px;
    }

  </style>
</head>
<body>
  <div class="print-btn" style="position: fixed; top: 0; left: 0; padding: 10px; background-color: white;">
    <button id="print-button" onclick="window.print()">Print</button>
    <button onclick="window.close()">Close</button>
  </div>
  
  <div class="table-wrapper">
           
  <div class="header">
  <!-- Icon in the first row -->
  <div class="icon">
 
  </div>

  <!-- Span Container -->
  <div class="span-container">
      <span class="span1">${user}</span>
      <span class="span2">Season :${saledetails[0].season}</span>
      <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
  </div>
  <div></div>
</div>
  <hr style="border-top: 1px dotted #ccc;">
</div>
</div>
  `
   categories.forEach(category => {
    invoices.forEach(invoice => {
      const categoryData = data.filter(item => item.factory_name === category && item.factory_name === invoice);

      if (categoryData.length > 0) {
        let totalSales = 0;
        let totalPkgs = 0;
let totalinvSales = 0;
        tableHTML += `
         
              
              <div class="table-wrapper">
           
                <h2 class="factory-name">FACTORY: ${category} </h2>
                <div class="tbl-data">
                <table>
                  <thead>
                    <tr>
                      <th>Sl</th>
                      <th>Lot</th>
                      <th>Grade</th>
                      <th>Weight/bag</th>
                      <th>Pkg</th>
                      <th>Total</th>
                    </tr>
                  </thead>
                  <tbody>
        `;

        categoryData.forEach((item, index) => {
          tableHTML += `
            <tr>
              <td>${index + 1}</td>
              <td>${item.lot}</td>
              <td>${item.grade}</td>
              <td>${item.net_weight}</td>
              <td>${item.pkgs}</td>
              <td>${item.gross_total}</td>
            
            </tr>
          `;

          totalSales += Number(item.gross_total);
          totalinvSales += Number(item.totalkg);
          totalPkgs += Number(item.pkgs);
        });

        tableHTML += `
                  </tbody>
                  <tfoot>
                    <tr>
                    <th>INV KG ${totalinvSales}</th>
                      <td></td>
                      <td></td>
                      <td></td>
                      <td class="top-border bottom-lines">${totalPkgs} Pkg</td>
                      <td class="top-border bottom-lines">${totalSales} Kg</td>
                    </tr>
                  </tfoot>
                </table>
                </div>
                <caption> ${tableCount}</caption>
              </div>
              <script>
                function printTable() {
                  window.print();
                }
              </script>
            </body>
          </html>
        `;

        tableCount++;
      }
    });
  });

  const newTab = window.open('', '_blank');
  newTab.document.body.innerHTML = tableHTML;


}

const OpenHTMLButton = ({ data,user ,saledetails}) => {
  const handleClick = () => {
    generateCategoryTable(data,user,saledetails)
  };


  return<button className="btn btn-primary w-100 mb-2 d-flex justify-content-between align-items-center"  onClick={handleClick} fdprocessedid="z00614">
  <span className="text-center mx-auto">Catalouge</span>
  <span className="icon my-auto">&#x1F512;</span>
</button>

};

export default OpenHTMLButton;
