import React,{useState} from 'react';
const AuctionSummaryBuyer = ({ data ,user,saledetails}) => {

  const [totalLeafPkgs, setTotalLeafPkgs] = useState(0);
  const [totalLeafKg, setTotalLeafKg] = useState(0);
  const [totalDustPkgs, setTotalDustPkgs] = useState(0);
  const [totalDustKg, setTotalDustKg] = useState(0);
  const [totalSupplementPkgs, setTotalSupplementPkgs] = useState(0);
  const [totalSupplementKg, setTotalSupplementKg] = useState(0);
  const [totalAmount, setTotalAmount] = useState(0);
  const [totalAvgAmount, setTotalAvgAmount] = useState(0);

  const groupAndCalculate = (data) => {
    const result = {};

    let totalLeafKg = 0;
    let totalDustKg = 0;
    let totalSupplementKg = 0;
    let totalAmount = 0;
    let totalAvgAmount = 0;

    for (const item of data) {
      const { buyer_name, category, totalkg, pkgs, gross_total, maximum_bid } = item;

      if (!result[buyer_name]) {
        result[buyer_name] = {
          leaf: { pkgs: 0, kg: 0, amount: 0 },
          dust: { pkgs: 0, kg: 0, amount: 0 },
          supplement: { pkgs: 0, kg: 0, amount: 0 },
          gross_total: 0,
        };
      }

      switch (category) {
        case 'LEAF':
          result[buyer_name].leaf.pkgs += Number(pkgs);
          result[buyer_name].leaf.kg += Number(totalkg);
          result[buyer_name].leaf.amount += Number(maximum_bid * gross_total);
          totalLeafKg += Number(totalkg);
          break;
        case 'DUST':
          result[buyer_name].dust.pkgs += Number(pkgs);
          result[buyer_name].dust.kg += Number(totalkg);
          result[buyer_name].dust.amount += Number(maximum_bid * gross_total);
          totalDustKg += Number(totalkg);
          break;
        case 'SUPPLEMENT':
          result[buyer_name].supplement.pkgs += Number(pkgs);
          result[buyer_name].supplement.kg += Number(totalkg);
          result[buyer_name].supplement.amount += Number(maximum_bid * gross_total);
          totalSupplementKg += Number(totalkg);
          break;
        default:
          break;
      }

      result[buyer_name].gross_total += Number(gross_total);
      totalAmount += Number(maximum_bid * gross_total);
      totalAvgAmount += Number(maximum_bid);
    }

    setTotalLeafKg(totalLeafKg);
    setTotalDustKg(totalDustKg);
    setTotalSupplementKg(totalSupplementKg);
    setTotalAmount(totalAmount);
    setTotalAvgAmount(totalAvgAmount);

    return result;
  };

  const handleButtonClick = () => {
    const groupedData = groupAndCalculate(data);

    // Calculations for totals, similar to the previous implementation...
    // You can directly calculate totals here without using useState and useEffect

    const dataAsHtml = `
    <table>
    <thead>
    <tr>
      <th rowSpan="2">Buyer Name</th>
      <th colSpan="2">Leaf</th>
      <th colSpan="2">Dust</th>
      <th colSpan="2">Supplement</th>
      <th rowSpan="2">Total Amount</th>
      <th rowSpan="2">Avg Amount</th>
    </tr>
    <tr>
      <th>Pkgs</th>
      <th>Kg</th>
      <th>Pkgs</th>
      <th>Kg</th>
      <th>Pkgs</th>
      <th>Kg</th>
    </tr>
  </thead>
      <tbody>
        ${Object.keys(groupedData).map((buyer_name) => {
          const { leaf, dust, supplement, gross_total } = groupedData[buyer_name];
          const totalAmount = leaf.amount + dust.amount + supplement.amount;
          const avgAmount = totalAmount / gross_total;

          return `
            <tr>
              <td>${buyer_name}</td>
              <td>${leaf.pkgs}</td>
              <td>${leaf.kg}</td>
              <td>${dust.pkgs}</td>
              <td>${dust.kg}</td>
              <td>${supplement.pkgs}</td>
              <td>${supplement.kg}</td>
              <td>${totalAmount.toFixed(2)}</td>
              <td>${avgAmount.toFixed(2)}</td>
            </tr>
          `;
        }).join('')}
      </tbody>
      <tfoot>
        <tr>
          <td>Total</td>
          <td>${totalLeafPkgs}</td>
          <td>${totalLeafKg}</td>
          <td>${totalDustPkgs}</td>
          <td>${totalDustKg}</td>
          <td>${totalSupplementPkgs}</td>
          <td>${totalSupplementKg}</td>
          <td>${totalAmount}</td>
          <td>${totalAvgAmount.toFixed(2)}</td>
        </tr>
      </tfoot>
    </table>
  `;
  const htmlContent = `
  <html>
  <head>
    <title>Buyer Summary</title>
    <link rel="icon"  type="image/png" href="/ttab.png" />
    <style>
      .table-wrapper {
        margin: 0 auto;
        width: 980px;
        padding: 20px;
        background-color: #ffffff;
      }
      body {
        font-family: "Courier New", Courier, monospace;
        color: #000;
        background: #757677;
        font-size: 12px;
      }
      .head {
        padding: 0px 50px;
        display: flex;
        justify-content: space-between;
      }
      table {
        width: 940px;
        margin: 0 auto;
        border-collapse: collapse;
      }
      th,
      td {
        padding: 5px;
        border: 1px solid black;
        text-align: center;
      }
      th.align-right,
      td.align-right {
        text-align: right;
      }
      .total {
        font-weight: bold;
        color: purple;
      }
      .green-text {
        font-weight: 400;
        color: purple;
      }
      .red-text {
        color: red;
      }
      .season {
        display: flex;
        align-items: space-between;
        justify-content: center;
      }
      @media print {
        body {
          margin: 0;
          font-size: 10px;
        }
        .table-wrapper {
          page-break-inside: avoid;
        }
      }
      .header {
         
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    /* Icon in the first row */
    .icon {
        width: 60px;
        height: 60px;
           }

    /* Span Container */
    .span-container {
        display: flex;
        flex-direction: column;
        align-items: center; /* Center the container horizontally */
    }

    /* Individual Spans */
    .span1, .span2, .span3 {
        padding: 2px 10px;
        text-align: center;
    }

    .span1 {
        font-size: 26px;
        font-weight:bold;
    }

    .span2, .span3 {
        font-size: 14px;
    }
    </style>
  </head>
  <body>
  <div class="table-wrapper">
  <div class="header">
  <!-- Icon in the first row -->
  <div class="icon">
  <img class="icon" src="/ttab.png" alt="Icon Image" />
  </div>

  <!-- Span Container -->
  <div class="span-container">
      <span class="span1">${user}</span>
      <span class="span2">Season :${saledetails[0].season}</span>
      <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
  </div>
  <div></div>
</div>
      <hr>
      ${dataAsHtml}
    </div>
   
  </body>
</html>
`;
  const newWindow = window.open();
  newWindow.document.write(htmlContent);
  newWindow.document.close();
}

  return (
    <button
      className="btn btn-primary w-100 text-center mb-2 d-flex justify-content-between align-items-center"
      onClick={handleButtonClick}
    >
      <span className="text-center mx-auto">Summary Buyer</span>
      <span className="icon my-auto">&#x1F512;</span>
    </button>
  );
};

export default AuctionSummaryBuyer;


