import React from 'react';

const BrokersSummary = ({ data ,user,saledetails}) => {
  // Create a dictionary to store the data for each broker
  const brokersData = {};

  // Calculate the data for each broker
  data.forEach((entry) => {
    const { brokers_name, pkgs, sold_status, gross_total, maximum_bid } = entry;
    if (sold_status === 1) {
      brokersData[brokers_name] = {
        totalLots: (brokersData[brokers_name]?.totalLots || 0) + pkgs,
        soldLots: (brokersData[brokers_name]?.soldLots || 0) + (sold_status ? pkgs : 0),
        offeredKg: (brokersData[brokers_name]?.offeredKg || 0) + parseFloat(gross_total),
        soldKg: (brokersData[brokers_name]?.soldKg || 0) + (sold_status ? parseFloat(gross_total) : 0),
        totalAmount: (brokersData[brokers_name]?.totalAmount || 0) + (sold_status ? parseFloat(gross_total) * maximum_bid : 0),
        totalVAT: (brokersData[brokers_name]?.totalVAT || 0) + (sold_status ? parseFloat(gross_total) * maximum_bid * 0.15 : 0),
      };
    }
  });

  // Convert the data into HTML
  const dataAsHtml = Object.keys(brokersData)
    .map((brokerName) => {
      const broker = brokersData[brokerName];
      const soldPercentage = (broker.soldKg / broker.offeredKg) * 100;
      return `
        <tr>
          <td>${brokerName}</td>
          <td>${broker.totalLots}</td>
          <td>${broker.soldLots}</td>
          <td>${broker.offeredKg.toFixed(2)}</td>
          <td>${broker.soldKg.toFixed(2)}</td>
          <td>${broker.totalAmount.toFixed(2)}</td>
          <td>${broker.totalVAT.toFixed(2)}</td>
          <td>${soldPercentage.toFixed(2)}%</td>
        </tr>
      `;
    })
    .join('');

  const handleButtonClick = () => {
    const htmlContent = `
    <html>
    <head>
      <title>Broker Summary</title>
      <link rel="icon"  type="image/png" href="/ttab.png" />
      <style>
        .table-wrapper {
          margin: 0 auto;
          width: 980px;
          padding: 20px;
          background-color: #ffffff;
        }
        body {
          font-family: "Courier New", Courier, monospace;
          color: #000;
          background: #757677;
          font-size: 12px;
        }
        .head {
          padding: 0px 50px;
          display: flex;
          justify-content: space-between;
        }
        table {
          width: 940px;
          margin: 0 auto;
          border-collapse: collapse;
        }
        th,
        td {
          padding: 5px;
          border: 1px solid black;
          text-align: center;
        }
        th.align-right,
        td.align-right {
          text-align: right;
        }
        .total {
          font-weight: bold;
          color: purple;
        }
        .green-text {
          font-weight: 400;
          color: purple;
        }
        .red-text {
          color: red;
        }
        .season {
          display: flex;
          align-items: space-between;
          justify-content: center;
        }
        @media print {
          body {
            margin: 0;
            font-size: 10px;
          }
          .table-wrapper {
            page-break-inside: avoid;
          }
        }
        .header {
         
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      /* Icon in the first row */
      .icon {
          width: 60px;
          height: 60px;
             }

      /* Span Container */
      .span-container {
          display: flex;
          flex-direction: column;
          align-items: center; /* Center the container horizontally */
      }

      /* Individual Spans */
      .span1, .span2, .span3 {
          padding: 2px 10px;
          text-align: center;
      }

      .span1 {
          font-size: 26px;
          font-weight:bold;
      }

      .span2, .span3 {
          font-size: 14px;
      }
      </style>
    </head>
    <body>
    <div class="table-wrapper">
    <div class="header">
    <!-- Icon in the first row -->
    <div class="icon">
    <img class="icon" src="/ttab.png" alt="Icon Image" />
    </div>

    <!-- Span Container -->
    <div class="span-container">
        <span class="span1">${user}</span>
        <span class="span2">Season :${saledetails[0].season}</span>
        <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
    </div>
    <div></div>
</div>
        <hr>  <table>
    <thead>
      <tr>
        <th>Broker Name</th>
        <th>Total Lots (pkgs)</th>
        <th>Sold Lots (pkgs)</th>
        <th>Offered KG</th>
        <th>Sold KG</th>
        <th>Total Amount</th>
        <th>Total VAT</th>
        <th>Sold%</th>
      </tr>
    </thead>
    <tbody>
        ${dataAsHtml}
        </tbody>
        <tfoot>
            <tr>
              <td class="total">Total</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.totalLots, 0)}</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.soldLots, 0)}</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.offeredKg, 0).toFixed(2)}</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.soldKg, 0).toFixed(2)}</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.totalAmount, 0).toFixed(2)}</td>
              <td class="total">${Object.values(brokersData).reduce((acc, cur) => acc + cur.totalVAT, 0).toFixed(2)}</td>
              <td class="total"></td>
            </tr>
          </tfoot>
        </table>
      </div>
     
    </body>
  </html>
    `;

    const newWindow = window.open();
    newWindow.document.write(htmlContent);
    newWindow.document.close();
  };

  return (
    
      <button className="btn btn-primary w-100 text-center mb-2 d-flex justify-content-between align-items-center" onClick={handleButtonClick}>
        <span className="text-center mx-auto">Brokers Summary</span>
  <span className="icon my-auto">&#x1F512;</span>
      </button>
    
  );
};

export default BrokersSummary;
