import React from 'react';

const FactorySold = ({ data,user,saledetails }) => {
  // ... (same calculations as before)
  const soldData = data.filter((item) => item.sold_status === 1);

  // Calculate the total sold packages for each factory
  const factorySoldPkgs = soldData.reduce((acc, item) => {
    const factoryName = item.factory_name;
    if (!acc[factoryName]) {
      acc[factoryName] = 0;
    }
    acc[factoryName] += item.pkgs;
    return acc;
  }, {});

  // Calculate the total sold kilograms for each factory
  const factorySoldKg = soldData.reduce((acc, item) => {
    const factoryName = item.factory_name;
    if (!acc[factoryName]) {
      acc[factoryName] = 0;
    }
    acc[factoryName] += parseFloat(item.gross_total);
    return acc;
  }, {});

  // Calculate the total amount (gross_total * maximum_bid) for each factory
  const factoryTotalAmount = soldData.reduce((acc, item) => {
    const factoryName = item.factory_name;
    if (!acc[factoryName]) {
      acc[factoryName] = 0;
    }
    acc[factoryName] += parseFloat(item.gross_total) * parseFloat(item.maximum_bid);
    return acc;
  }, {});

  // Calculate the average price (total amount / gross_total) for each factory
  const factoryAvgPrice = Object.keys(factoryTotalAmount).reduce((acc, factoryName) => {
    acc[factoryName] = factoryTotalAmount[factoryName] / factorySoldKg[factoryName];
    return acc;
  }, {});
  const handleButtonClick = () => {
    const dataAsHtml = `
      <h2 style="text-align: center;">Factory Wise Sold Report</h2>
      <table>
        <thead>
          <tr>
            <th>Factory Name</th>
            <th>Total Sold Packages</th>
            <th>Total Sold Kg</th>
            <th>Total Amount</th>
            <th>Average Price</th>
          </tr>
        </thead>
        <tbody>
          ${Object.keys(factorySoldPkgs)
            .map((factoryName) => {
              return `
                <tr>
                  <td>${factoryName}</td>
                  <td>${factorySoldPkgs[factoryName]}</td>
                  <td>${factorySoldKg[factoryName].toFixed(2)}</td>
                  <td>${factoryTotalAmount[factoryName].toFixed(2)}</td>
                  <td>${factoryAvgPrice[factoryName].toFixed(2)}</td>
                </tr>
              `;
            })
            .join('')}
        </tbody>
      </table>
    `;
    const htmlContent = `
    <html>
    <head>
      <title>Factory Wise Sold</title>
      <link rel="icon"  type="image/png" href="/ttab.png" />
      <style>
        .table-wrapper {
          margin: 0 auto;
          width: 980px;
          padding: 20px;
          background-color: #ffffff;
        }
        body {
          font-family: "Courier New", Courier, monospace;
          color: #000;
          background: #757677;
          font-size: 12px;
        }
        .head {
          padding: 0px 50px;
          display: flex;
          justify-content: space-between;
        }
        table {
          width: 940px;
          margin: 0 auto;
          border-collapse: collapse;
        }
        th,
        td {
          padding: 5px;
          border: 1px solid black;
          text-align: center;
        }
        th.align-right,
        td.align-right {
          text-align: right;
        }
        .total {
          font-weight: bold;
          color: purple;
        }
        .green-text {
          font-weight: 400;
          color: purple;
        }
        .red-text {
          color: red;
        }
        .season {
          display: flex;
          align-items: space-between;
          justify-content: center;
        }
        @media print {
          body {
            margin: 0;
            font-size: 10px;
          }
          .table-wrapper {
            page-break-inside: avoid;
          }
        }
        .header {
         
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      /* Icon in the first row */
      .icon {
          width: 60px;
          height: 60px;
             }

      /* Span Container */
      .span-container {
          display: flex;
          flex-direction: column;
          align-items: center; /* Center the container horizontally */
      }

      /* Individual Spans */
      .span1, .span2, .span3 {
          padding: 2px 10px;
          text-align: center;
      }

      .span1 {
          font-size: 26px;
          font-weight:bold;
      }

      .span2, .span3 {
          font-size: 14px;
      }
      </style>
    </head>
    <body>
    <div class="table-wrapper">
    <div class="header">
    <!-- Icon in the first row -->
    <div class="icon">
    <img class="icon" src="/ttab.png" alt="Icon Image" />
    </div>

    <!-- Span Container -->
    <div class="span-container">
        <span class="span1">${user}</span>
        <span class="span2">Season :${saledetails[0].season}</span>
        <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
    </div>
    <div></div>
</div>
        <hr>
        ${dataAsHtml}
      </div>
     
    </body>
  </html>
    `;
    const newWindow = window.open();
    newWindow.document.write(htmlContent);
    newWindow.document.close();
  };

  return (
      <button className="btn btn-primary w-100 text-center mb-2 d-flex justify-content-between align-items-center" onClick={handleButtonClick}>
      
       <span className="text-center mx-auto"> Factory Sold</span>
  <span className="icon my-auto">&#x1F512;</span>
      </button>
  );
};

export default FactorySold;
